<template>
    <div class="qs">
        <div class="h-headtitle">快速上手</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                通过本章节你可以了解到 war-wolf-ui 的安装方法和基本使用姿势。
            </div>
        </div>

        <div class="h-headtitle">安装</div>
        <div class="h-blocks">
            <div class="h-title">通过 npm 安装</div>
            <div class="h-main">
                在现有项目中使用 war-wolf-ui 时，可以通过 npm 进行安装：
            </div>
            <div v-highlight>
                <pre>
                    <code>
        # Vue 2 项目：
        npm i war-wolf-ui -S
                    </code>
                </pre>
            </div>
        </div>

        <div class="h-headtitle">引入组件</div>
        <div class="h-blocks">
            <div class="h-title">完整引入</div>
            <div class="h-main">
                在 main.js 中写入以下内容：
            </div>
            <div v-highlight>
                <pre>
                    <code>
        import WarWolfUI from 'war-wolf-ui'
        import 'war-wolf-ui/lib/war-wolf-ui.css'
        import 'war-wolf-ui/lib/iconfont.css' //非必要 （如果用到图片库，要引入）
        Vue.use(WarWolfUI);
                    </code>
                </pre>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: 'QuickStart',
        data() {
            return {

            }
        },
        component:{

        }
    }
</script>
<style scoped>

</style>



